const sidebarMenuButtonVariants = cva(
  'peer/menu-button flex w-full items-center gap-3 overflow-hidden rounded-md p-2 text-left text-sm outline-none ring-sidebar-ring transition-[width,height,padding,color,background-color,box-shadow,transform] hover:bg-sidebar-accent hover:text-sidebar-accent-foreground focus-visible:ring-2 active:bg-sidebar-accent active:text-sidebar-accent-foreground disabled:pointer-events-none disabled:opacity-50 group-has-[[data-sidebar=menu-action]]/menu-item:pr-8 aria-disabled:pointer-events-none aria-disabled:opacity-50 data-[active=true]:bg-sidebar-primary data-[active=true]:font-medium data-[active=true]:text-sidebar-primary-foreground data-[state=open]:bg-sidebar-accent data-[state=open]:text-sidebar-accent-foreground group-data-[collapsible=icon]:justify-center group-data-[collapsible=icon]:!size-10 group-data-[collapsible=icon]:!p-0 [&>span:last-child]:truncate [&>svg]:size-5 [&>svg]:shrink-0',
  {
    variants: {
      variant: {
        default: 'hover:bg-sidebar-accent hover:text-sidebar-accent-foreground',
        outline:
          'bg-transparent shadow-[0_0_0_1px_hsl(var(--sidebar-border))] hover:bg-sidebar-accent hover:text-sidebar-accent-foreground hover:shadow-[0_0_0_1px_hsl(var(--sidebar-accent))]',
      },
      size: {
        default: 'h-10 text-sm',
        sm: 'h-8 text-xs',
        lg: 'h-12 text-sm',
      },
      isActive: {
        true: 'bg-sidebar-primary text-sidebar-primary-foreground font-medium',
        false: '',
      },
    },
    defaultVariants: {
      variant: 'default',
      size: 'default',
      isActive: false,
    },
  }
);

const SidebarMenuButton = React.memo(
  React.forwardRef<
    HTMLButtonElement,
    React.ComponentProps<'button'> & {
      asChild?: boolean;
      tooltip?: string | React.ComponentProps<typeof TooltipContent>;
    } & VariantProps<typeof sidebarMenuButtonVariants>
  >(
    (
      {
        asChild = false,
        isActive = false,
        variant = 'default',
        size = 'default',
        tooltip,
        className,
        ...props
      },
      ref
    ) => {
      const Comp = asChild ? Slot : 'button';
      const { isMobile, state } = useSidebar();

      const button = (
        <Comp
          ref={ref}
          data-sidebar="menu-button"
          data-size={size}
          data-active={isActive}
          className={cn(
            sidebarMenuButtonVariants({ variant, size, isActive }),
            'hover-glow',
            className
          )}
          {...props}
        />
      );

      if (!tooltip) {
        return button;
      }

      // Tooltip logic prevents full memoization if tooltip is an object literal
      const tooltipProps =
        typeof tooltip === 'string' ? { children: tooltip } : tooltip;

      return (
        <Tooltip>
          <TooltipTrigger asChild>{button}</TooltipTrigger>
          <TooltipContent
            side="right"
            align="center"
            sideOffset={6}
            hidden={state !== 'collapsed' || isMobile}
            className={cn(
              'bg-popover text-popover-foreground px-2 py-1 rounded text-xs',
              tooltipProps?.className
            )} // Apply default tooltip style + potential override
            {...tooltipProps} // Spread potentially dynamic object
          />
        </Tooltip>
      );
    }
  ),
  (prevProps, nextProps) => {
    // Custom comparison function for React.memo
    // Only re-render if relevant props change
    return (
      prevProps.asChild === nextProps.asChild &&
      prevProps.isActive === nextProps.isActive &&
      prevProps.variant === nextProps.variant &&
      prevProps.size === nextProps.size &&
      prevProps.className === nextProps.className &&
      prevProps.children === nextProps.children && // Check children if not simple strings
      // Simple tooltip string comparison
      (typeof prevProps.tooltip === 'string' &&
        typeof nextProps.tooltip === 'string' &&
        prevProps.tooltip === nextProps.tooltip) ||
      // More complex comparison if tooltip is an object (might still cause re-renders if object identity changes)
      (typeof prevProps.tooltip !== 'string' &&
        typeof nextProps.tooltip !== 'string' &&
        JSON.stringify(prevProps.tooltip) ===
          JSON.stringify(nextProps.tooltip))
      // Note: Comparing complex objects like this can be fragile. A stable tooltip prop structure is better.
    );
  }
);
SidebarMenuButton.displayName = 'SidebarMenuButton';

