// Memoize sub button if props are stable
const SidebarMenuSubButton = React.memo(
  React.forwardRef<
    HTMLAnchorElement,
    React.ComponentProps<'a'> & {
      asChild?: boolean;
      size?: 'sm' | 'md';
      isActive?: boolean;
    }
  >(({ asChild = false, size = 'md', isActive, className, ...props }, ref) => {
    const Comp = asChild ? Slot : 'a';

    return (
      <Comp
        ref={ref}
        data-sidebar="menu-sub-button"
        data-size={size}
        data-active={isActive}
        className={cn(
          'flex h-8 min-w-0 items-center gap-2 overflow-hidden rounded-md px-2 text-sidebar-foreground/80 outline-none ring-sidebar-ring hover:bg-sidebar-accent hover:text-sidebar-accent-foreground focus-visible:ring-2 active:bg-sidebar-accent active:text-sidebar-accent-foreground disabled:pointer-events-none disabled:opacity-50 aria-disabled:pointer-events-none aria-disabled:opacity-50 [&>span:last-child]:truncate [&>svg]:size-4 [&>svg]:shrink-0 [&>svg]:text-sidebar-accent-foreground hover-glow',
          'data-[active=true]:bg-sidebar-accent data-[active=true]:text-sidebar-accent-foreground',
          size === 'sm' && 'text-xs h-7',
          size === 'md' && 'text-sm h-8',
          'group-data-[collapsible=icon]:hidden',
          className
        )}
        {...props}
      />
    );
  })
);
SidebarMenuSubButton.displayName = 'SidebarMenuSubButton';

